import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthenticationGuard } from 'core/services/authentication.service';
import { AuthorizationGuard } from 'core/services/authorization.service';
import { ApplyWfhComponent } from './apply-wfh/apply-wfh.component';
import { ReviewWfhComponent } from './review-wfh/review-wfh.component';
import { ListWfhComponent } from './list-wfh/list-wfh.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Work from Home'
    },
    children: [
      {
        path: '',
        redirectTo: 'list'
      },
      {
        path: 'list',
        data: {
          title: 'List',
          access: {
            entity: 'work_from_home:work_from_home',
            action: 'find:get'
          }
        },
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        component: ListWfhComponent
      },
      {
        path: 'review',
        data: {
          title: 'Review',
          access: {
            entity: 'work_from_home:work_from_home',
            action: 'approve:put'
          }
        },
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        component: ReviewWfhComponent
      },
      {
        path: 'apply',
        data: {
          title: 'Apply',
          access: {
            entity: 'work_from_home:work_from_home',
            action: 'create:post'
          }
        },
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        component: ApplyWfhComponent
      },
      {
        path: 'policy',
        loadChildren: './policy/wfh-policy.module#WfhPolicyModule'
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WorkFromHomeRoutingModule { }
