import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';

import { Wfh, WfhService } from 'modules/work-from-home/work-from-home.service';
import { STATUS } from 'config/constants/wfh';
import { Authorization } from 'core/services/authorization.service';

@Component({
  selector: 'app-list-wfh',
  templateUrl: './list-wfh.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [WfhService, Authorization]
})
export class ListWfhComponent implements OnInit {

  wfhs: Wfh[] = [];

  applyAllowed: boolean = false;
  reviewAllowed: boolean = false;

  cancelling: Array<Boolean>;

  constructor(
    private wfhService: WfhService,
    private authorization: Authorization
  ) {

    this.cancelling = new Array<Boolean>();
  }

  async ngOnInit() {

    this.applyAllowed = await this.authorization.hasAccess({
      entity: 'work_from_home:work_from_home',
      action: 'create:post'
    });

    this.reviewAllowed = await this.authorization.hasAccess({
      entity: 'work_from_home:work_from_home',
      action: 'approve:put'
    });

    this.loadWfhs();
  }

  loadWfhs() {

    const self = this;
    this.wfhService.getWfhs().then(wfhs => {

      this.wfhs = wfhs;
    });
  }

  cancelWfh(index) {

    const wfh_id = this.wfhs[index]._id;
    const self = this;

    this.cancelling[index] = true;
    this.wfhService.cancel(wfh_id).then(response => {

      self.wfhs[index].wfh_status = STATUS.DELETED;
      self.cancelling[index] = false;
    });
  }

  getStatusBadge(status) {

    switch (status) {

      case 2: return 'badge-success';
      case 3: return 'badge-danger';
      case 4: return 'badge-dark';
      default: return 'badge-default';
    }
  }
}
