import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

// import { TYPE } from 'config/constants/wfh';
import { Api } from 'core/services/api.service';

class ReviewedBy {

  user_id: string;
  name: string
};

export class ApplyWfh {

  date: string = '';
  summary: string = '';
};

export class Wfh {

  _id: string;
  user_id: string;
  name: string;
  date: string;
  summary: string;
  reviewed_by: ReviewedBy;
  wfh_status: number;
};

@Injectable()
export class WfhService extends Api {

  private _base_url = '/wfhs';

  constructor(http: HttpClient) {
    super(http);
  }

  apply(wfh: ApplyWfh): Promise<Object> {

    return this.post(this._base_url, wfh)
      .then(response => response);
  }

  getWfhs(query?: Map<string, any>): Promise<Wfh[]> {

    return this.get(this._base_url, query)
      .then(response => response['data'] as Wfh[]);
  }

  approve(wfh_id: string): Promise<Object> {

    return this.put(this._base_url + '/approve/' + wfh_id, {})
      .then(response => response);
  }

  reject(wfh_id: string): Promise<Object> {

    return this.put(this._base_url + '/reject/' + wfh_id, {})
      .then(response => response);
  }

  cancel(wfh_id: string): Promise<Object> {

    return this.delete(this._base_url + '/cancel/' + wfh_id)
      .then(response => response);
  }
}
