import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import { DatePipe } from '@angular/common';

import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';

import { ApplyWfh, WfhService } from 'modules/work-from-home/work-from-home.service';
import { BsDatepickerDirective } from 'ngx-bootstrap';

const error_map = {
  date: 'Date',
  summary: 'Summary'
};

@Component({
  selector: 'app-apply-wfh',
  templateUrl: './apply-wfh.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [WfhService, DatePipe]
})
export class ApplyWfhComponent implements OnInit {
  @ViewChild('errorModal') errorModal: ErrorModalComponent;
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('date') date: BsDatepickerDirective;

  wfh: ApplyWfh;

  dateConfig: Object;

  error: string;
  invalid_attributes: string[];

  constructor(
    private wfhService: WfhService,
    private datePipe: DatePipe
  ) {

    this.wfh = new ApplyWfh();

    this.dateConfig = {
      dateInputFormat: 'YYYY-MM-DD',
      containerClass: 'theme-blue',
      showWeekNumbers: false
    };
  }

  private __validate() {
    return true;
  }

  private __sanitizeWfh(wfh) {

    if (wfh.date !== undefined) {
      wfh.date = this.datePipe.transform(wfh.date, 'yyyy-MM-dd');
    }

    return wfh;
  }

  async ngOnInit() {
  }

  applyWfh() {

    if (this.__validate()) {

      let wfh = JSON.parse(JSON.stringify(this.wfh));
      wfh = this.__sanitizeWfh(wfh);
      this.error = '';
      this.invalid_attributes = [];

      this.wfhService.apply(wfh).then(data => {

        this.successModal.show('/work-from-home/list');

      }).catch(err => {

        this.error = err.error.message.text;
        if (err.error.invalid_attributes) {
          for (const key in err.error.invalid_attributes) {
            if (err.error.invalid_attributes.hasOwnProperty(key)) {
              this.invalid_attributes.push(error_map[key] ? error_map[key] : err.error.invalid_attributes[key]);
            }
          }
        }
        this.errorModal.show();
      });
    }
  }
}
